.cartDisplay{
    width: 90%;
    margin: 0 auto;
    /* background: red; */
    padding: 10px;
}
.cartDisplay-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* background: red; */
    padding: 10px;
    /* color: #fff; */
}
.cartDisplay-header h2{
    font-family: sans-serif;
    font-size: 30px;
    font-weight: bold;
    color: #fff;
}
.cartDisplay-header p{
    color: #fff;
    font-size: 40px;
    cursor: pointer;
}
.cartDisplay-header p:hover{
    color: #000;
}